/*node-sass --watch --recursive --output css*/

@import "node_modules/bootstrap/scss/functions";
@import "node_modules/bootstrap/scss/variables";
@import "node_modules/bootstrap/scss/mixins";




$nav-link-disabled-color:           $blue;
$navbar-dark-toggler-border-color : transparent;
$navbar-dark-toggler-icon-bg : url("data:image/svg+xml;charset=utf8,%3Csvg viewBox='0 0 32 32' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath stroke='rgba(149, 88, 8, 0.75)' stroke-width='2' stroke-linecap='round' stroke-miterlimit='10' d='M4 8h24M4 16h24M4 24h24'/%3E%3C/svg%3E");
$body-bg: #141519;
$body-color: white;



$dropdown-min-width: 0;
$dropdown-bg:	$dark;
$jumbotron-bg: 						rgba(149, 88, 8, 0.24);

body {
    min-width: 320px;
}
.navbar{
	box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.5);
}

li.open .dropdown-menu .dropdown:hover .dropdown-menu {
	display: block;
	top: -25%;
	right: 100%;
}

li.open .dropdown-menu .dropdown .caret {
	display: none;
}

li.open .dropdown-menu .dropdown > a:before {
	display:block;
	content:" ";
	float:left;
	width:0;
	height:0;
	border-color:transparent;
	border-style:solid;
	border-width:5px 5px 5px 0;
	border-right-color:#999;
	margin-top:5px;
	margin-left:-10px;
}





@import "node_modules/bootstrap/scss/root";
